.cryptography{
    display: flex;
    justify-content: center;
    margin: 1rem;
}

.cryptography h3{
    padding: 1rem;
    background: #52616b;
    border-radius: 0.5rem;
    margin: auto;
    width: 50%;
    text-align: center;
}

.cards {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.card {
    padding: 1rem;
    width: 20vw;
    border: 1px solid #ccc;
    box-shadow: 2px 2px 6px 0px rgba(0, 0, 0, 0.3);
    background: #c9d6df;
    border-radius: 1rem;
}

.card img {
    max-width: 100%;
}

.card .text {
    padding: 0 20px 20px;
}

.card .text>button {
    background: gray;
    border: 0;
    color: white;
    padding: 10px;
    width: 100%;
}