.nav ul img{
    width: 2vw;
    filter: brightness(0) invert(1);
}

.nav ul img:hover{
    cursor: pointer;
    filter: invert(40%) grayscale(100%) brightness(100%) sepia(100%) hue-rotate(-50deg) saturate(400%) contrast(2);
}

.nav ul{
    list-style-type: none;
    margin: 0;
}

.nav li {
    display: flex;
    align-items: center;
}

.nav{
    background: #1e2022;
}

.nav ul li p{
    color: #f0f5f9;
    padding: .5rem;
    font-size: 1.5rem;
    font: sans-serif;
}

svg{
    fill: white;
}