.descripcion{
    width: 40vw;
}

.curriculum a{
    display: block;
    border: 0;
    background: hsl(204, 13%, 37%);
    color: rgb(211, 211, 211);
    padding: 1rem;
    width: 7vw;
    border-radius: .5rem;
    text-decoration: none;
    text-align: center;
    margin: .5rem;
}

.curriculum a:hover{
    cursor: pointer;
    background: red;
}

.hola{
    font-weight: bold;
}

.animado{
    font-size: 2rem;
}

.foto img{
    border-radius: 50%;
    width: 15vw;
}

.introduccion{
    display: flex;
    flex-direction: row;
    padding: 2rem;
    justify-content: space-between;
    background: #c9d6df;
}

.redes{
    display: flex;
    width: 10vw;
    justify-content: space-between;
}

.redes img{
    width: 2vw;
}

.redes img:hover{
    cursor: pointer;
    filter: invert(40%) grayscale(100%) brightness(55%) sepia(100%) hue-rotate(-50deg) saturate(400%) contrast(2);
}